.single-card-main{
    display: flex;
    flex-flow: row nowrap;
   
    width: 483px;
    height: 278px;
    margin: 15px 0;
}  
.single-card-info-block{
    display: flex;
    flex-flow: column nowrap;
    justify-content: space-between;
} 
.single-card-img{
    flex: 0 0 175px;
    height: 278px;
    border: 1px solid #000;

}
.single-card-content{
    padding: 15px !important;
}
.single-card-action{
   margin: 0 !important;
   margin-bottom: auto;

   border-top: 1px solid #ddd;
}


.spinner {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(255,255,255, 0.7);
    display: flex;
}

.spinner .spin {
    margin: auto;
    display: block;
    width: 170px;
    height: 150px;
    background-image: url(/movies-react/static/media/logo-db.02a9430b.svg);
    background-position: 50% 50%;
    background-size: cover;
    background-repeat: no-repeat;
    z-index: 100;
}

.movies-sorting-block{
    display: flex;
   justify-content: flex-end;

    padding: 5px 0px;
}
.movies-sorting-block .movies-select{
    margin: 10px 0;
    width: 200px;
}
.paiging-btn{
    display: flex;
    /* justify-content: center; */
    text-align: center;
}
.paiging-btn-group{
    width: 100%;
    display: flex;
    justify-content: space-evenly;
}
.paiging-btn-group button {
    background: transparent;    
    border-radius: 50%;
    outline: none;
    border: none;
    margin: 0;
    padding: 0;
}
.paiging-btn-group i{
    font-size: 50px !important;
}
.list-container  {
    display: flex;
    flex-flow: row wrap;
    justify-content: space-evenly;
    align-items: flex-start;

}
.block-sorting{
    height: 50px;
}



header{
  position: fixed;
  width: 100vw;
  top: 0;
  left: 0;
  background-color: #282c34;    
  z-index: 99;
}
.app-logo {
  height: 65px;
}
  
.app-header {
  max-height: 10vh;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  color: white;
  padding: 3px 0 !important;
}

.app-header-nav{
  margin: 0 15px;
}

.app-nav{
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
  list-style: none;
  padding: 0 !important;
}
 
.app-nav li {
  font-size: 16px;
  margin: 8px 10px; 
}

.app-nav a{ 
  color: #fff;
  text-decoration: none;
} 

.app-link {
  color: #61dafb;
}

  
