.content-container{
    max-width: 1000px;
    margin: auto;
    height: 90vh;
    display: flex;
}
.smp-card-wrapper{
    display: flex;
    flex-flow: row nowrap;
    
}
.smp-card{
    margin: auto;
    /* border: 3px solid #fafafa; */
    background-color: rgba(232,232,232, 0.2)!important;
    color: #fafafa !important;
    width: 100%;
    z-index: 0;
    
}
.smp-card-media{
    flex: 0 0 300px;
    height: 450px;
}

.smp-card-content{
    flex: 1 1;
    position: relative;
}
.smp-card-title{
    margin-bottom: 30px;
    color: #fff !important;
}
.smp-card-title h1 {
    max-height: 75px;
    overflow: hidden;
    text-overflow: ellipsis;
}

.smp-btn-back{
    outline: none;
    background-color: none;
    border: 1px solid black;
    border-radius: 50%;
    width: 40px;
    height: 40px;

    position: absolute !important;  
    top: 0;
    right: 0;
}
.smp-btn-back i {
    line-height: 15px !important;
    color: #fff !important;
}

.smp-card-descr p{ 
    color: #fff !important;
    max-height: 240px;
    overflow: hidden;
    text-overflow: ellipsis;
}

.bg-customer{
    background-color:rgba(0,0,0, 0.65);
}
